.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;

  padding: 0.5rem;
  margin: 1rem 0;

  background-color: #e6e5e9;
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: rgb(0, 0, 0);
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: rgb(255, 255, 255);
  border: 1px solid #d3cece;

  background-color: #625b77;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 12px;
}
.expense-item__price:hover {
  transform: scale(1.01);
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
